main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #8c54ff;
  --secondary: #f3a847;
  --tertiary: #b12704;
  --light: #fafafa;
  --dark: #000000;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
  
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: 0.1em;
  color: var(--darker);
  font-family: 'Poiret One', cursive;
  background-image: url(./assets/background.jpeg);
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 60px;
  padding-top: 150px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

.nav-item a:hover {
  text-decoration: underline;
}

header {
  background: var(--dark);
  top: 0;
  height: 150px;
  z-index: 5;
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: 0.8;
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

img {
  max-width: 100%;
}

span[role='img'] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  cursor: pointer;
}

textarea {
  width: 600px;
  height: 120px;
  border: 3px solid #cccccc;
  padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
  background: rgba(0, 0, 0, 0.65);
  bottom: 0;
  left: 0;
  overflow: auto;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1;
}

.modalContainer {
  background: var(--darker);
  border-radius: 5px;
  max-width: 100%;
  margin: 50px auto;
  padding: 15px;
  width: 960px;
}

.modalTitle {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 25px;
}

button {
  background: var(--primary);
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 30px;
  padding: 10px 15px;
}

a {
  border-radius: 5px;
  color: white;
  font-weight: 600;
  padding: 2px 4px;
  margin-left: -2px;
  margin-right: -2px;
}

.navActive {
  color: var(--secondary);
}

#nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* background-image: url(./assets/AdobeStock_301046542.jpeg);
  background-size:cover; */
  padding: 30px;
  background-repeat: no-repeat;
}

header {
  background-image: url(./assets/AdobeStock_301046542.jpeg);
  background-size: cover;
  position: fixed;
  width: 100%;
  top: 0;
}

header h1 {
  color: white;
  text-align: center;
  padding-top: 5px;
}

.nav-item {
  margin-right: 75px;
}

#about {
  padding-left: 20px;
}

/* styling for work section */
.flexbox-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 20px;
  width: 75%;
  height: min-content;
  z-index: 1;
}

.flexbox-item {
  border: 3px solid red;
  height: 300px;
  margin: 5px;
  padding: 50px 0;
  color: white;
  overflow: hidden;
  opacity: .8;
  position: relative;
}

.flexbox-item:hover {
  opacity: 1;
  border-radius: 20px;
}

.flexbox-item-1 {
  width: 100%;  
  padding: 130px 0;
  background-image: url(./assets/anime.jpg);
  background-size:cover;
  
}

.flexbox-item-2 {
  background-image: url(./assets/ambience.jpeg);
  background-size: cover;
  background-position: center;
}

.flexbox-item-3 {
  background-image: url(./assets/AdobeStock_269021997.jpeg);
  background-size: cover;
  background-position: top;
}

.flexbox-item-4 {
  background-image: url(./assets/AdobeStock_279212049.jpeg);
  background-size: cover;
  background-position: center;
}

.flexbox-item-5 {
  background-image: url(./assets/AdobeStock_377749814.jpeg);
  background-size: cover;
  background-position: center;
}

.flexbox-item-6 {
  background-image: url(./assets/AdobeStock_83490178.jpeg);
  background-size: cover;
  background-position:center;
}

.flexbox-item-2, .flexbox-item-3, .flexbox-item-4, .flexbox-item-5, .flexbox-item-6 {
  width: 40%;
  height: 150px;
  flex-grow: 1;
  flex-shrink: 1;
}
#dep-link:hover {
  text-decoration: underline;
}
#work h1 {
  padding-left: 20px;
}

#work h4, #work p {
  /* background-image: linear-gradient(navy, yellow); */
  width: min-content;
  color: white;
  background-color: black;
  border-radius: 10px;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Styling for contact page */
.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  text-align: center;
}

.card {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  padding: 0px 5px;
  width: 50%;
  margin-bottom: 10px;
  border-radius: 10px;
}

/* On mouse-over, add a deeper shadow */
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.contact-container {
  text-align: center;
  margin: 0px 20px;
  padding: 10px 20px;
  margin-top: 20px;
  background-color: #f2a300;
}

.contact-container input, .contact-container textarea {
  width: 250%;
  height: 30px;
  margin: 10px 0px;
}

.contact-container textarea {
  height: 50px;
}

.contact-container button {
  width: 250%;
  background-color: #051b42;
}



#git-icon {
  background-color: black;
  border-radius: 30px;
  font-size: 25px;
  position: absolute;
  top: 0px;
  right: 0px;
}

#git-icon:hover {
  background-color: #15497b;
}

#page div h1 {
  color: #051b42;
  font-weight: 600;
  text-shadow: 1px 1px 2px black, 0 0 25px #f2a300, 0 0 5px white;
}



/* #contact {
  padding-left: 20px;
} */

footer {
  position: fixed; 
  padding: 10px 10px 0px 10px; 
  bottom: 0;
  width: 100%; 
  /* Height of the footer*/  
  height: 60px; 
  background: #08244c; 
  opacity: .7;
}

footer:hover {
  opacity: 1 ;
}

#footer-icon-container {
  display: flex;
  justify-content: center;
}

footer span {
  margin-left: 20px;
  font-size: 30px;
}

footer span:hover {
  background-color: black;
  border-radius: 4px;
}

#resume {
  padding-left: 20px;
}

#resume div a {
  color: black;
  font-size: 25vh;
}

#resume div a:hover {
  color: yellow;
}

#resume h2 {
  border-bottom: 5px solid black;
  width: fit-content;
  font-size: 1.5em;
}

.contact-container h2 {
  background-color: #051b42;
  border-radius: 5px;
  color: white;
  font-weight: 600;
  text-shadow: 1px 1px 2px black, 0 0 25px black, 0 0 5px orange;
}

/* MEDIA QUERY FOR TABLETS AND SMALLER */
@media screen and (max-width: 768px) {
  .flexbox-container {
    width: 100%;
  }

  .contact-container {
    width: 75%;
  }

  #nav-container {
    display: flex;
    flex-wrap: inherit;
    justify-content: center;
  }

  .nav-item {
    padding: 0px;
    margin: 0px;
    text-align: center;
  }

  .nav-item a {
    padding: 10px;
    margin: 0px;
  }

  header h1{
    width: 100%;
    color: white;
  }

  h1, p, #headshot {
    text-align: center;
  }

  h1 {
    text-align: center;
  }
}
